import myFetch from '@/utils/myFetch'
import Qs from 'qs' 
export function getAllConcepts(pTypeCode,pTerm){
	var param=Qs.stringify({
	});
	return  myFetch({
		method:'post',
		url:'/concept/queryAllConcept.do',
		data:param
	});
}
export function addConcept(pId,pOriginName){
	var param=Qs.stringify({
		id:pId,
		'originName':pOriginName
	});
	return  myFetch({
		method:'post',
		url:'/concept/addConcept.do',
		data:param
	});
}
export function updateConcept(pId,pConceptId,pConceptName){
	var param=Qs.stringify({
		id:pId,
		'conceptId':pConceptId,
		'conceptName':pConceptName
	});
	return  myFetch({
		method:'post',
		url:'/concept/updateConcept.do',
		data:param
	});
}
export function getStandName(pConceptId){
	var param=Qs.stringify({
		'conceptId':pConceptId
	});
	return  myFetch({
		method:'post',
		url:'/concept/getSingleConcept.do',
		data:param
	});
}
export function updateStandName(pStandName,pConceptId){
	var param=Qs.stringify({
		'newStandName':pStandName,
		'conceptId':pConceptId
	});
	return  myFetch({
		method:'post',
		url:'/concept/updateStandName.do',
		data:param
	});
}


